<template>
<main class="container">
  <Content/>
  <section class="w-75 mx-auto mb-5" v-if="count">
    <SelectMode v-model:mode="mode"/>
    <p class="my-5 text-center" v-if="mode">complétez le formulaire ci-dessous afin que nous puissions continuer la validation de votre commande</p>
    <form class="my-4" v-if="mode">
      <aside v-if="mode=='delivery'" class="alert alert-info">
        <p>pour pouvoir profiter d'une livraison, votre commande doit avoir un montant minimum par rapport à la distance</p>
        <p>ci dessous, la liste des montants minimum par rapport à la distance:</p>
        <ul>
          <li v-for="(p, i) in prices" :key="i">{{p.distance}}km, {{p.amount}}&euro;</li>
        </ul>
      </aside>
      <fieldset>
        <div class="mb-3">
          <label>nom</label>
          <input type="text" id="name" class="form-control" :disabled="canDeliver"/>
        </div>
        <div class="mb-3">
          <label>e-mail</label>
          <input type="email" id="email" class="form-control" :disabled="canDeliver"/>
        </div>
        <div class="mb-3">
          <label>numéro de téléphone</label>
          <input type="text" id="phone" class="form-control" :disabled="canDeliver"/>
        </div>
      </fieldset>
      <fieldset v-if="mode == 'delivery'">
        <div class="mb-3">
          <label>adresse</label>
          <input type="text" id="street" class="form-control" :disabled="canDeliver"/>
        </div>
        <div class="row mb-3">
          <div class="col-9">
            <label>ville</label>
            <input type="text" id="city" class="form-control" :disabled="canDeliver"/>
          </div>
          <div class="col">
            <label>code postal</label>
              <input type="number" id="zipcode" class="form-control" :disabled="canDeliver"/>
          </div>
        </div>
      </fieldset>
      <div class="mb-3">
        <label>date de retrait/livraison désirée</label>
        <input type="date" id="when" class="form-control" v-model="when" :disabled="canDeliver"/>
      </div>
    </form>
  </section>
</main>
</template>
<script>
import {mapGetters} from 'vuex'
import Content from './Content.vue'
import SelectMode from './SelectMode.vue'

export default {
  name: "cart",
  data() {
    let now = new Date()
    return {
      mode: "",
      confirmed: false,
      when: now.toISOString().split('T')[0], //now.toLocaleDateString(),
    }
  },
  computed: {
    ...mapGetters("cart", ["count", "deliverable", "prices"]),
    canDeliver() {
      if (this.mode != "delivery") {
        return false
      }
      return this.mode == "delivery" && this.deliverable ? false: 'disabled'
    },
  },
  components: {
    Content,
    SelectMode,
  }
}
</script>
